<template>
  <div>
    <!--    -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
<!--    -->
    <el-card>
<!--      -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="dialogVisible=true"></el-button>
        </el-col>
      </el-row>
<!--      -->
      <el-table :data="rolesList" border stripe>
<!--        -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row :class="['border_bottom',index===0 ? 'border_top': '','center']" v-for="(item,index) in scope.row.children" :key="item.id">
<!--              -->
              <el-col :span="5">
                <el-tag closable @close="removeRightById(scope.row,item.id)">{{item.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
<!--              -->
              <el-col :span="19">
                <el-row :class="[index1===0 ? '' :'border_top','center']" v-for="(item1,index1) in item.children" :key="item1.id">
                  <el-col :span="6">
                    <el-tag closable @close="removeRightById(scope.row,item1.id)" type="success">{{item1.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag type="warning" closable @close="removeRightById(scope.row,item2.id)" v-for="(item2,index2) in item1.children" :key="item2.id">
                      {{item2.authName}}
                    </el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
<!--        -->
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="300px">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.id)"></el-button>
            <el-button size="mini" type="danger" icon="el-icon-delete" @click="removeRolesById(scope.row.id)"></el-button>
            <el-button size="mini" type="warning" icon="el-icon-setting" @click="showSetRightDialog(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--    -->
    <el-dialog title="" :visible.sync="dialogVisible" width="40%" @close="addDialogClosed">
      <!--      -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
        <el-form-item label="" prop="roleName" label-width="80px">
          <el-input v-model="addForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc" label-width="80px">
          <el-input v-model="addForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="addRole"> </el-button>
      </span>
    </el-dialog>
    <!--    -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="30%" @close="editDialogClose">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
        <el-form-item label="" prop="roleName" label-width="80px">
          <el-input v-model="editForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc" label-width="80px">
          <el-input v-model="editForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editRoleInfo"> </el-button>
      </span>
    </el-dialog>
<!--    -->
    <el-dialog title="" :visible.sync="setDialogVisible" width="50%" @close="setDialogClose">
<!--      -->
      <el-tree :data="rightsList" :props="treeProps" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys" ref="treeRef"></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setDialogVisible = false"> </el-button>
        <el-button type="primary" @click="allRights"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "Roles",
  data(){
    return{
      rolesList:[],
      dialogVisible:false,
      addForm:{
        roleName:'',
        roleDesc:''
      },
      addFormRules:{
        roleName:[
          {required: true, message: '', trigger: 'blur'},
          {min: 3, max: 10, message: ' 3  10 ', trigger: 'blur'}
        ],
        roleDesc:[
          {required: true, message: '', trigger: 'blur'},
          {min: 6, max: 15, message: ' 6  15 ', trigger: 'blur'}
        ],
      },
      editDialogVisible:false,
      editForm:{

      },
      editFormRules:{
        roleName:[
          {required: true, message: '', trigger: 'blur'},
          {min: 3, max: 10, message: ' 2  10 ', trigger: 'blur'}
        ],
        roleDesc:[
          {required: true, message: '', trigger: 'blur'},
          {min: 6, max: 15, message: ' 5  15 ', trigger: 'blur'}
        ],
      },
      setDialogVisible:false,
    //  
      rightsList:[],
      //
      treeProps:{
        label:'authName',
        children:'children'
      },
      //
      defKeys:[],
      //id
      roleId:''
    }
  },
  created() {
    this.getRolesList()
  },
  methods:{
    async getRolesList(){
      const {data:res}=await this.$http.get('roles')
      if (res.meta.status!==200){
        return this.$message.error('')
      }
      this.rolesList=res.data

      console.log(this.rolesList)
    },
    addDialogClosed(){
      this.$refs.addFormRef.resetFields();
    },
    addRole(){
      this.$refs.addFormRef.validate(async valid=>{
        if (!valid)
          return
        const {data:res}=await this.$http.post('roles',this.addForm)
        if (res.meta.status!==201){
          this.$message.error('')
        }
        this.$message.success('')
        //
        this.dialogVisible=false
        //
        this.getRolesList()
      })
    },
    editDialogClose(){
      this.$refs.editFormRef.clearValidate();
    },
    async showEditDialog(id){
      const {data:res}=await this.$http.get('roles/'+id)
      if(res.meta.status!==200){
        return this.$message.error('')
      }
      this.editForm=res.data
      console.log(this.editForm)
      this.editDialogVisible=true
    },
    editRoleInfo(){
      this.$refs.editFormRef.validate(async valid=>{
        if (!valid)
          return
        // 
        const {data:res}=await this.$http.put('roles/'+this.editForm.roleId,{
          roleName:this.editForm.roleName,
          roleDesc:this.editForm.roleDesc
        })
        if (res.meta.status!==200){
          return this.$message.error('')
        }
        //
        this.editDialogVisible=false
        //
        this.getRolesList();
        //
        this.$message.success('')
      })
    },
    async removeRolesById(id){
      // 
      const res=await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err=>err)
      //confirm cancel
      // console.log(res)
      if (res!=='confirm'){
        return this.$message.info('')
      }
      const {data:response}=await this.$http.delete('roles/'+id)
      if (response.meta.status!==200){
        return this.$message.error('')
      }
      this.$message.success('')
      this.getRolesList();
    },
    // Id
    async removeRightById(role,rightId){
      const confirmResult=await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err=>err)
      if (confirmResult!=='confirm'){
        return this.$message.info('')
      }
      const {data:res}=await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
      if (res.meta.status!==200){
        return this.$message.error('')
      }
      this.$message.success('')
      // this.getRolesList();
      role.children=res.data
    },
    //
    async showSetRightDialog(role){
      this.roleId=role.id
      //
      const {data:res}=await this.$http.get('rights/tree')
      if (res.meta.status!==200){
        return this.$message.error('')
      }
      this.rightsList=res.data
      console.log(this.rightsList)
      this.getLeafKeys(role,this.defKeys)
      this.setDialogVisible=true
    },
    // iddefKeys
    getLeafKeys(node,arr){
      if (!node.children){
        return arr.push(node.id)
      }
      node.children.forEach(item=>
      this.getLeafKeys(item,arr)
      )
    },
    setDialogClose(){
      this.defKeys=[]
    },
    //
    async allRights(){
      const keys=[
          ...this.$refs.treeRef.getCheckedKeys(),
          ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr=keys.join(',')

      const {data:res}=await this.$http.post(`roles/${this.roleId}/rights`,{
        rids:idStr
      })
      if (res.meta.status!=200){
        return this.$message.error('')
      }
      this.$message.success('')
      this.getRolesList()
      this.setDialogVisible=false
    }
  }
}
</script>

<style lang="less" scoped>
  .el-tag{
    margin: 7px;
  }
  .border_top{
    border-top: 1px solid #eeeeee;
  }
  .border_bottom{
    border-bottom: 1px solid #eeeeee;
  }
  .center{
    display: flex;
    align-items: center;
  }
</style>